 body {
     font-family: 'Poppins', sans-serif;
     background-color: white;
     min-height: 100vh;
     display: flex;
     flex-direction: column;
}
/* AppBar */
 .app-bar {
     padding: 8px 16px;
     background-color: white;
     display: grid;
     grid-template-columns: auto 1fr auto;
     gap: 10px;
     position: sticky;
     top: 0;
     z-index: 99;
     box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 .app-bar .app-bar__menu {
     display: flex;
     align-items: center;
}
 .app-bar .app-bar__menu button {
     background-color: transparent;
     border: none;
     font-size: 18px;
     padding: 8px;
     cursor: pointer;
}
 .app-bar .app-bar__brand {
     display: flex;
     align-items: center;
}
 .app-bar .app-bar__brand h1 {
     color: #db0000;
     text-transform: uppercase;
     font-size: 22px;
     user-select: none;
}
 .app-bar .app-bar__navigation {
     position: absolute;
     top: 50px;
     left: -180px;
     width: 150px;
     transition: all 0.3s;
     padding: 8px;
     background-color: white;
     overflow: hidden;
}
 .app-bar .app-bar__navigation.open {
     left: 0;
}
 .app-bar .app-bar__navigation ul li a {
     display: inline-block;
     text-decoration: none;
     color: black;
     padding: 8px;
     margin-bottom: 5px;
     width: 100%;
}
/* Main Content */
 main {
     padding: 32px;
     flex: 1;
}
 .content {
     margin: 0 auto;
     min-height: 100%;
}
 .content .content__heading {
     font-weight: normal;
     text-align: center;
}
/* Movies */
 .movies {
     display: grid;
     grid-template-columns: 1fr;
     gap: 16px;
     margin: 32px 0;
}
/* Movie Item */
 .movie-item {
     width: 100%;
     box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
     border-radius: 8px;
     overflow: hidden;
}
 .movie-item__header {
     position: relative;
}
 .movie-item .movie-item__header .movie-item__header__poster {
     width: 100%;
}
 .movie-item .movie-item__header .movie-item__header__rating {
     position: absolute;
     padding: 8px;
     bottom: 20px;
     left: 0;
     display: inline-block;
     background-color: black;
     color: white;
}
 .movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {
     margin-left: 10px;
}
 .movie-item .movie-item__content {
     padding: 16px;
}
 .movie-item .movie-item__content h3 {
     margin: 0 0 10px 0;
}
 .movie-item .movie-item__content h3 a {
     color: #db0000;
     text-decoration: none;
}
 .movie-item .movie-item__content p {
     overflow: hidden;
     text-overflow: ellipsis;
     display: -webkit-box;
     -webkit-line-clamp: 4;
    /* number of lines to show */
     line-clamp: 4;
     -webkit-box-orient: vertical;
}
/* Movie */
 .movie {
     margin: 0 auto;
     width: 100%;
     max-width: 800px;
     display: grid;
     grid-template-columns: 1fr;
     gap: 18px 16px;
}
 .movie .movie__poster {
     width: 100%;
     max-width: 400px;
}
 .movie .movie__info h4 {
     margin: 8px 0;
}
/* Footer */
 footer {
     padding: 16px;
}
 footer #footerTools {
     display: flex;
     flex-direction: column;
     justify-content: center;
     gap: 8px;
}
 footer p {
     margin-top: 16px;
     text-align: center;
     color: #aaaaaa;
}
 footer p a {
     color: #db0000;
     text-decoration: none;
}
/* Button */
 .btn {
     display: inline-block;
     padding: 12px 24px;
     background-color: #DB0000;
     border: 1px solid #770000;
     border-radius: 4px;
     font-weight: 500;
     font-size: 16px;
     color: #ffffff;
     cursor: pointer;
     transition: .15s ease-in-out;
}
 .btn:hover {
     background-color: #DB0000BB;
}
 .btn:active {
     background-color: #DB0000DD;
}
 .btn:focus-visible {
     outline: 3px solid #DB000055;
}
/*==================== VARIABLES CSS ====================*/
 :root {
    /*========== Colors ==========*/
     --hue-color: 190;
     --first-color: hsl(var(--hue-color), 69%, 61%);
     --first-color-second: hsl(var(--hue-color), 69%, 61%);
     --first-color-alt: hsl(var(--hue-color), 57%, 53%);
     --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
     --title-color: hsl(var(--hue-color), 8%, 15%);
     --text-color: hsl(var(--hue-color), 8%, 45%);
     --text-color-light: hsl(var(--hue-color), 8%, 65%);
     --input-color: hsl(var(--hue-color), 70%, 96%);
     --body-color: hsl(var(--hue-color), 60%, 99%);
     --container-color: #fff;
     --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
     --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);
    /*========== Font and typography ==========*/
     --body-font: "Poppins", sans-serif;
     --big-font-size: 2rem;
     --h1-font-size: 1.5rem;
     --h2-font-size: 1.25rem;
     --h3-font-size: 1.125rem;
     --normal-font-size: 0.938rem;
     --small-font-size: 0.813rem;
     --smaller-font-size: 0.75rem;
    /*========== Font weight ==========*/
     --font-medium: 500;
     --font-semi-bold: 600;
    /*========== Margenes Bottom ==========*/
     --mb-0-25: 0.25rem;
     --mb-0-5: 0.5rem;
     --mb-0-75: 0.75rem;
     --mb-1: 1rem;
     --mb-1-5: 1.5rem;
}
/* Font size for large devices */
 @media screen and (min-width: 968px) {
     :root {
         --big-font-size: 3rem;
         --h1-font-size: 2.25rem;
         --h2-font-size: 1.5rem;
         --h3-font-size: 1.25rem;
         --normal-font-size: 1rem;
         --small-font-size: 0.875rem;
         --smaller-font-size: 0.813rem;
    }
}
/*==================== BASE ====================*/
 * {
     box-sizing: border-box;
     padding: 0;
     margin: 0;
}
 html {
     scroll-behavior: smooth;
}
 body {
     margin: 0 0 var(--header-height) 0;
     font-family: var(--body-font);
     font-size: var(--normal-font-size);
     background-color: var(--body-color);
     color: var(--text-color);
}
 h1, h2, h3, h4 {
     color: var(--title-color);
     font-weight: var(--font-semi-bold);
}
/*==================== REUSABLE CSS CLASSES ====================*/
 .section {
     padding: 2rem 0 4rem;
     margin-bottom: 40px;
}
 .section__title {
     font-size: var(--h1-font-size);
}
 .section__subtitle {
     display: block;
     font-size: var(--small-font-size);
     margin-bottom: var(--mb-2);
}
 .section__title, .section__subtitle {
     text-align: center;
}
/*==================== LAYOUT ====================*/
 .container {
     max-width: 768px;
     margin-left: var(--mb-1-5);
     margin-right: var(--mb-1-5);
}
 .grid {
     display: grid;
     gap: 1.5rem;
}
 .header {
     width: 100%;
     position: fixed;
     bottom: 0;
     left: 0;
     z-index: var(--z-fixed);
     background-color: var(--body-color);
}
/*==================== HOME ====================*/
 .home__container {
     gap: 1rem;
}
 .home__content {
     grid-template-columns: 0.5fr 3fr;
     align-items: center;
}
 .home__social {
     display: grid;
     grid-template-columns: max-content;
     row-gap: 1rem;
}
 .home__social-icon {
     font-size: 1.25rem;
     color: var(--first-color);
}
 .home__social-icon:hover {
     color: var(--first-color-alt);
}
 .home__blob {
     width: 200px;
     background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
     background-size: contain;
     background-repeat: no-repeat;
}
 .home__data {
     grid-column: 1/3;
}
 .home__title {
     font-size: var(--big-font-size);
}
 .home__subtitle {
     font-size: var(--h3-font-size);
     color: var(--text-color);
     font-weight: var(--font-medium);
     margin-bottom: var(--mb-0-75);
}
 .home__description {
     margin-bottom: var(--mb-2);
}

 .about__description {
     text-align: center;
}
 .movie-item__detail-button {
     margin-top: 10px;
     padding: 10px 15px;
     background-color: #007BFF;
     color: white;
     border: none;
     border-radius: 5px;
     cursor: pointer;
     text-align: center;
     font-size: 16px;
}
 .movie-item__detail-button a {
     text-decoration: none;
     color: white;
}
 .movie-item__detail-button:hover {
     background-color: #0056b3;
}
 .search-input {
     padding: 10px;
     width: 80%;
     display: block;
     margin: 20px auto;
     font-size: 16px;
     border-radius: 5px;
     border: 1px solid #ccc;
}
 .search-input:focus {
     outline: none;
     border-color: #007bff;
}
 .pagination {
     display: flex;
     justify-content: center;
     align-items: center;
     gap: 20px;
     margin-top: 20px;
}
 .pagination__button {
     background-color: #007bff;
     color: white;
     border: none;
     padding: 10px 20px;
     font-size: 16px;
     border-radius: 5px;
     cursor: pointer;
     transition: background-color 0.3s ease, transform 0.2s ease;
}
 .pagination__button:hover {
     background-color: #0056b3;
     transform: scale(1.1);
}
 .pagination__button:disabled {
     background-color: #cccccc;
     cursor: not-allowed;
}
 .pagination__button#previousPage {
     background-color: #28a745;
}
 .pagination__button#nextPage {
     background-color: #dc3545;
}
 .pagination__button#previousPage:hover {
     background-color: #218838;
}
 .pagination__button#nextPage:hover {
     background-color: #c82333;
}
 .pagination span {
     font-size: 18px;
     font-weight: bold;
     color: #333;
}
 .movie__actions {
     margin-top: 20px;
     text-align: center;
}
 .movie__imdb-button {
     width: 100%;
     background-color: #f5c518;
     color: white;
     padding: 12px 24px;
     font-size: 16px;
     border: none;
     border-radius: 5px;
     cursor: pointer;
     text-align: center;
     display: inline-block;
     transition: background-color 0.3s ease, transform 0.2s ease;
     text-decoration: none;
}
 .movie__imdb-button:hover {
     background-color: #e0a800;
     transform: scale(1.05);
}
 .movie__imdb-button:focus {
     outline: none;
}
 .movie__imdb-button:active {
     background-color: #c89e00;
}
/*========== SCROLL BAR ==========*/
 ::-webkit-scrollbar {
     width: 0.6rem;
     background-color: var(--scroll-bar-color);
     border-radius: 0.5rem;
}
 ::-webkit-scrollbar-thumb {
     background-color: var(--scroll-thumb-color);
     border-radius: 0.5rem;
}
 ::-webkit-scrollbar-thumb:hover {
     background-color: var(--text-color-light);
}
/*==================== MEDIA QUERIES ====================*/
/* For small devices */
 @media screen and (max-width: 380px) {
     .container {
         margin-left: var(--mb-1);
         margin-right: var(--mb-1);
    }
     .home__content {
         grid-template-columns: 0.35fr 3fr;
    }
     .home__blob {
         width: 250px;
    }
     .home__social-icon {
         width: 30px;
    }
     .home__subtitle {
         font-size: 24px;
    }
     .home__description {
         font-size: 18px;
    }
}
/* For medium devices */
 @media screen and (min-width: 568px) {
     .home__content {
         grid-template-columns: max-content 1fr 1fr;
    }
     .home__data {
         grid-column: initial;
    }
     .home__img {
         order: 1;
         justify-self: center;
    }
     .about__container {
         grid-template-columns: repeat(2, 1fr);
    }
     @media screen and (min-width: 768px) {
         .container {
             margin-left: auto;
             margin-right: auto;
        }
         .body {
             margin: 0;
        }
         .section {
             padding: 6rem 0 2rem;
        }
         .section__subtitle {
             margin-bottom: 4rem;
        }
         .header {
             top: 0;
             bottom: initial;
        }
         .header, .main, .footer__container {
             padding: 0 1rem;
        }
         .home__container {
             row-gap: 5rem;
        }
         .home__content {
             padding-top: 5.5rem;
             column-gap: 2rem;
        }
         .home__blob {
             width: 270px;
        }
         .home__scroll {
             display: block;
        }
         .about__container {
             column-gap: 5rem;
        }
         .about__img {
             width: 350px;
        }
         .about__info {
             justify-content: space-between;
        }
         .about__buttons {
             justify-content: initial;
        }
    }
}
/* For large devices */
 @media screen and (min-width: 1024px) {
     body {
         margin: 0;
    }
     .header, .main, .footer__container {
         padding: 0;
    }
     .home__blob {
         width: 320px;
    }
     .home__social {
         transform: translateX(-6rem);
    }
