;\r\n     padding: 8px;\r\n     cursor: pointer;\r\n}\r\n .app-bar .app-bar__brand {\r\n     display: flex;\r\n     align-items: center;\r\n}\r\n .app-bar .app-bar__brand h1 {\r\n     color: #db0000;\r\n     text-transform: uppercase;\r\n     font-size: 22px;\r\n     user-select: none;\r\n}\r\n .app-bar .app-bar__navigation {\r\n     position: absolute;\r\n     top: 50px;\r\n     left: -180px;\r\n     width: 150px;\r\n     transition: all 0.3s;\r\n     padding: 8px;\r\n     background-color: white;\r\n     overflow: hidden;\r\n}\r\n .app-bar .app-bar__navigation.open {\r\n     left: 0;\r\n}\r\n .app-bar .app-bar__navigation ul li a {\r\n     display: inline-block;\r\n     text-decoration: none;\r\n     color: black;\r\n     padding: 8px;\r\n     margin-bottom: 5px;\r\n     width: 100%;\r\n}\r\n/* Main Content */\r\n main {\r\n     padding: 32px;\r\n     flex: 1;\r\n}\r\n .content {\r\n     margin: 0 auto;\r\n     min-height: 100%;\r\n}\r\n .content .content__heading {\r\n     font-weight: normal;\r\n     text-align: center;\r\n}\r\n/* Movies */\r\n .movies {\r\n     display: grid;\r\n     grid-template-columns: 1fr;\r\n     gap: 16px;\r\n     margin: 32px 0;\r\n}\r\n/* Movie Item */\r\n .movie-item {\r\n     width: 100%;\r\n     box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n     border-radius: 8px;\r\n     overflow: hidden;\r\n}\r\n .movie-item__header {\r\n     position: relative;\r\n}\r\n .movie-item .movie-item__header .movie-item__header__poster {\r\n     width: 100%;\r\n}\r\n .movie-item .movie-item__header .movie-item__header__rating {\r\n     position: absolute;\r\n     padding: 8px;\r\n     bottom: 20px;\r\n     left: 0;\r\n     display: inline-block;\r\n     background-color: black;\r\n     color: white;\r\n}\r\n .movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {\r\n     margin-left: 10px;\r\n}\r\n .movie-item .movie-item__content {\r\n     padding: 16px;\r\n}\r\n .movie-item .movie-item__content h3 {\r\n     margin: 0 0 10px 0;\r\n}\r\n .movie-item .movie-item__content h3 a {\r\n     color: #db0000;\r\n     text-decoration: none;\r\n}\r\n .movie-item .movie-item__content p {\r\n     overflow: hidden;\r\n     text-overflow: ellipsis;\r\n     display: -webkit-box;\r\n     -webkit-line-clamp: 4;\r\n    /* number of lines to show */\r\n     line-clamp: 4;\r\n     -webkit-box-orient: vertical;\r\n}\r\n/* Movie */\r\n .movie {\r\n     margin: 0 auto;\r\n     width: 100%;\r\n     max-width: 800px;\r\n     display: grid;\r\n     grid-template-columns: 1fr;\r\n     gap: 18px 16px;\r\n}\r\n .movie .movie__poster {\r\n     width: 100%;\r\n     max-width: 400px;\r\n}\r\n .movie .movie__info h4 {\r\n     margin: 8px 0;\r\n}\r\n/* Footer */\r\n footer {\r\n     padding: 16px;\r\n}\r\n footer #footerTools {\r\n     display: flex;\r\n     flex-direction: column;\r\n     justify-content: center;\r\n     gap: 8px;\r\n}\r\n footer p {\r\n     margin-top: 16px;\r\n     text-align: center;\r\n     color: #aaaaaa;\r\n}\r\n footer p a {\r\n     color: #db0000;\r\n     text-decoration: none;\r\n}\r\n/* Button */\r\n .btn {\r\n     display: inline-block;\r\n     padding: 12px 24px;\r\n     background-color: #DB0000;\r\n     border: 1px solid #770000;\r\n     border-radius: 4px;\r\n     font-weight: 500;\r\n     font-size: 16px;\r\n     color: #ffffff;\r\n     cursor: pointer;\r\n     transition: .15s ease-in-out;\r\n}\r\n .btn:hover {\r\n     background-color: #DB0000BB;\r\n}\r\n .btn:active {\r\n     background-color: #DB0000DD;\r\n}\r\n .btn:focus-visible {\r\n     outline: 3px solid #DB000055;\r\n}\r\n/*==================== VARIABLES CSS ====================*/\r\n :root {\r\n    /*========== Colors ==========*/\r\n     --hue-color: 190;\r\n     --first-color: hsl(var(--hue-color), 69%, 61%);\r\n     --first-color-second: hsl(var(--hue-color), 69%, 61%);\r\n     --first-color-alt: hsl(var(--hue-color), 57%, 53%);\r\n     --first-color-lighter: hsl(var(--hue-color), 92%, 85%);\r\n     --title-c